@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root{
  --primary-color: #fff;
  --secondary-color: #686868;
  --sidebar-primary-color: #015AE9;
  --premium-color: #ffdf29;
  --title-color: #141468;
  --bug-color: #22ce22;
  --border-primary: #e6e6e6;
  --icon-background: #f3f3f3;
  --warn-color: #ffd000;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
body {
  width: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app{
    display: flex;
    height: auto;
    width: 100%;
    min-height: 100vh;
}
.home{
    width: 100%;
}

.home__container{
    width: 95%;
    height: auto;
    min-height: 100vh;
    margin: auto;
}

.home__utils{
    display: flex;
}

.home__titleContainer{
    display: flex;
    align-items: center;
}

.home__title{
    padding-top: 10px;
    color: var(--title-color);
    flex: 1 1;
}

.home__acceptBugs{
    display: flex;
    align-items: center;
    margin-top: 20px;
    padding: 15px 24px;
    border-radius: 20px;
    border: none;
    background-color: var(--primary-color);
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    cursor: pointer;
}

.home__acceptBugs > svg{
    font-size: 16px;
    margin-left: 5px;
    color: var(--bug-color);
}

.overviewErrors__container{
    max-height: 80vh;
    margin-top: 10px;
    width: 80%;
    height: auto;
    display: grid;
    grid-template-columns: repeat(4, 200px);
    grid-template-rows: auto;
    grid-gap: 40px;
    border-radius: 20px;
}

.messageSender{
    width: 100%;
    height: 440px;
    border-radius: 15px;
    background-color: var(--primary-color);
    box-shadow: rgba(8, 129, 251, 0.342) 0px 0px 10px;
}

.messageSender__top{
    display: flex;
    align-items: center;
}

.messageSender__title{
    font-size: 18px;
    padding: 17px 10px;
    flex: 1 1;
}

.sendfile__option{
    position: relative;
    cursor: pointer;
}

.text__alignment > svg{
    margin-right: 20px;
    font-size: 20px;
    color: var(--sidebar-primary-color);
}

.sendfile__option > svg{
    position: absolute;
    cursor: pointer;
    margin-right: 10px;
    font-size: 20px;
    color: var(--sidebar-primary-color);
}

.sendfile__option > input{
    opacity: 0;
    cursor: pointer;
    width: 20px;
}

.messageSender__contact{
    width: 95%;
    background-color: var(--icon-background);
    padding: 10px 0px;
    margin: 0px auto;
    border-radius: 10px;
    cursor: pointer;
}

.messageSender__contact > p{
    padding-left: 10px;
    color: var(--secondary-color);
}

.messageSender__message{
    position: relative;
}

.message{
    width: 95%;
    height: 250px;
    margin: 20px 2.5%;
    border: 1px solid var(--border-primary);
    border-radius: 15px;
    outline: none;
    resize: none;
    padding: 10px 10px 80px 10px;
}

.messageOptions{
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    bottom: 24px;
    left: 2.5%;
    width: 95%;
    height: 60px;
    background-color: var(--primary-color);
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    border: 1px solid var(--border-primary);
}

.messageOptions >svg{
    font-size: 20px;
    cursor: pointer;
}

.message__send{
    width: 95%;
    height: 35px;
    margin: 0px 2.5%;
    border-radius: 10px;
    background-color: var(--sidebar-primary-color);
    color: #fff;
    font-size: 18px;
    border: none;
    cursor: pointer;
}
.widgets{
    width: 25%;
    height: 86vh;
    margin-top: 10px;
    border-radius: 20px;
}
.overviewItem{
    height: 200px;
    border-radius: 20px;
    border: 1px solid var(--border-primary);
    box-shadow: rgba(8, 129, 251, 0.342) 0px 0px 10px;
    padding: 10px;
    cursor: pointer;
}

.overviewItem__top{
    display: flex;
}

.overviewItem__image{
    width: 60px;
    height: 60px;
    min-height: 60px;
    min-width: 60px;
    border: 1px solid var(--border-primary);
    border-radius: 15px;
    display: grid;
    place-items: center;
}

.overviewItem__image > img{
    width: 50px;
    height: 50px;
    object-fit: scale-down;
    border-radius: 9px;
}

.overviewItem__options{
    display: flex;
    align-items: flex-start;
    padding-top: 0px;
}

.option__wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.overviewItem__back{
    margin: 0px 6px;
    width: 30px;
    height: 30px;
    background-color: var(--icon-background);
    border-radius: 50%;
    display: grid;
    place-items: center;
}


.overviewItem__name{
    font-size: 20px;
    padding-top: 5px;
    color: var(--title-color);
    font-weight: 400;
}

.overviewItem__description{
    padding: 5px 0px;
    width: 80%;
    font-size: 13px;
    color: var(--secondary-color);
}

.overviewItem__active{
    display: flex;
}

.active__item{
    font-size: 15px;
    flex: 1 1;
    color: var(--secondary-color);
}

.bug__count{
    color: var(--bug-color);
    font-weight: 500;
    font-size: 15px;
    opacity: 0.8;
}

.overviewItem__more{
    margin: 17px 5%;
    width: 90%;
    height: 30px;
    border-radius: 20px;
    background-color: var(--icon-background);
    transition: all 0.3s;
    border: none;
    cursor: pointer;
}

.overviewItem__more:hover{
    transition: all 0.3s;
    background-color: var(--border-primary);
}
@font-face {
    font-family: Futura;
    src: url("/errors/static/media/Futura Condensed Extra Bold.74aa3b0f.otf");
}
.sidebar{
    width: 140px;
    height: 100vh;
    background-color: var(--sidebar-primary-color);
}

.sidebar__container{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.sidebar__container > a{
    text-decoration: none;
}

.sidebar__collection{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1 1;
}

.sidebar__collectionItem{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.sidebar__logo{
    padding: 20px 0px 20px 0px;
    text-align: center;
}

.logo{
    font-family: Futura;
    color: var(--primary-color);
    font-size: 40px;
    letter-spacing: -6px;
    font-weight: bold;
}

.sidebar__collectionLink{
    margin: 5px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: var(--primary-color);
    width: 80%;
    height: 60px;
}

.sidebar__collectionItem > svg{
    font-size: 18px;
    margin-bottom: 5px;
}

.sidebar__avatarContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0px auto;
    padding-bottom: 20px;
    color: var(--primary-color);
}

.sidebar__accountInfo > span{
    font-size: 20px;
    font-weight: 600;
}

.sidebar__avatar{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    margin: 0px auto;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 10px;
}

.sidebar__premium{
    display: flex;
    align-items: center;
    color: var(--premium-color);
}

.sidebar__avatarPremium{
    font-weight: 500;
    color: var(--premium-color);
    padding: 10px 5px;
}

.active__sidebarLink{
    width: 80%;
    background-color: var(--primary-color);
    color: var(--sidebar-primary-color);
    border-radius: 10px;
}
.errors_header {
    width: 100%;
    height: 12vh;
}

.errors_header > .errors_wrapper {
    width: 88%;
    height: 12vh;
    margin: auto;
}

.errors_wrapper > .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 12vh;
}

.header > h3 {
    font-size: 30px;
    font-weight: bold;
}

.header > .addTask {
    display: flex;
    align-items: center;

}

.addTaskItem > button {
    border: none;
    height: 35px;
    padding: 0 20px;
    font-family: sans-serif;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    background-color: #015AE9;
    color: white;

}

.addTaskItem > select {
    border: none;
    height: 35px;
    width: 30px;
    border-left: 1px solid rgba(221, 221, 221, 0.486);
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    background-color: #015AE9;
    color: white;
    outline: none;
}

.addTask > .addTaskItem {
    border-radius: 8px;
}

.addTask_icons {
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #ddd;
    margin-left: 10px;
    border-radius: 8px;
    background-color: #fff;
    color: #000;
}
.personal_task {
    width: 100%;
    margin-top: 1vh;
    padding: 10px 0 3px 0;
}

.personal_task_wrapper {
    width: 100%;
    margin: auto;
}

.personal_task_wrapper > .tasks {
    display: flex;
    align-items: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.task_icons {
    padding: 0 5px 0 0;
    cursor: pointer;
}

.tasks > p {
    cursor: pointer;
}

.tasks > .hr {
    border: 0.2px solid rgba(204, 204, 204, 0.267);
    flex: 1 1;
    margin-left: 20px;
}

/* tasks_wrap */
.tasks_wrap {
    display: none;
}

.activeTasks {
    display: block;
}
.item_task {
    margin-top: 10px;
}
.tasks_id {
    width: 100%;
    margin-top: 2vh;
    padding: 3px 0 10px 0;
}

.tasks_id > .tasks_wrapper {
    width: 100%;
    margin: auto;
}

.tasks_wrapper > .projects {
    display: flex;
    align-items: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.projects > .project_tasks {
    display: flex;
    align-items: center;
}

.projects > .hr {
    flex: 1 1;
    border: 0.2px solid rgba(204, 204, 204, 0.267);
    margin: 0 20px;
}

.project_tasks > p {
    cursor: pointer;
}

.projects > .list_tasks {
    display: flex;
    align-items: center;
    border: 0.2px solid rgba(146, 146, 146, 0.267);
    padding: 2px 6px;
    border-radius: 8px;
    margin: 0 15px 0 0;
    cursor: pointer;
}

.list_tasks > p {
    font-size: 16px;
    padding: 0 8px 0 4px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.projects > .data_tasks {
    display: flex;
    align-items: center;
    border: 0.2px solid rgba(78, 78, 78, 0.267);
    padding: 2px 6px;
    border-radius: 8px;
    cursor: pointer;
}

.data_tasks > p {
    margin-left: 7px;
}

/* list_tasks_item */
.list_tasks_item {
    width: 100%;
    display: none;
}

.activelist_tasks_item {
    display: block;
}

/* tasks_search */
.tasks_search {
    width: 100%;
    margin-top: 3vh;
    display: none;
}

.activetasks_search {
    display: block;
}
.taskItem_wrapper {
    margin-top: 4vh;
}

.taskItem_wrapper > .tasks_item {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.tasks_item > .item_image {
    display: flex;
}

.item_image > .addfill {
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0.2px solid rgba(139, 139, 139, 0.267);
    border-radius: 50%;
    background-color: rgba(167, 167, 167, 0.267);
    cursor: pointer;
}
.tasks_item > h4 {
    font-size: 20px;
}

.item_image > .item_imageImg img {
    border: 0.2px solid rgba(139, 139, 139, 0.267);
    width: 25px;
    height: 25px;
    border-radius: 50%;
    cursor: pointer;
}
.interface {
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 0px 4px 0.2px rgb(177, 177, 177);
    border-radius: 6px;
}

.interface > .interface_wrapper {
    width: 96%;
    margin: auto;
    display: flex;
    padding: 2vh 0;
}

.interface_wrapper > .interLeft {
    width: 5%;
    display: flex;
    margin-right: 10px;
    padding: 3px;
}

.interLeft > .raundet {
    width: 25px;
    height: 25px;
    border: 2px solid rgba(129, 129, 129, 0.644);
    border-radius: 50%;
}

.interface_wrapper > .interRight {
    width: 100%;
}

.header_right {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header_right > .interRightItem {
    display: flex;
    align-items: center;
}

.interRight > .header_right > .interRightItem > .clock_reminder {
    display: flex;
    align-items: center;
    border: 0.2px solid rgba(66, 63, 63, 0.479);
    padding: 2px 6px;
    border-radius: 8px;
    cursor: pointer;
}

.interRight > .header_right > .interRightItem > .clock_reminder > p {
    margin-left: 5px;
}

.interRight > .header_right > .interRightItem > .priority_reminder {
    display: flex;
    align-items: center;
    border: 0.2px solid rgba(66, 63, 63, 0.479);
    padding: 2px 6px;
    border-radius: 8px;
    margin: 0 10px;
    cursor: pointer;
}

.interRight > .header_right > .interRightItem > .priority_reminder > p {
    margin-left: 7px;
}

.interRight > .header_right > .interRightItem > .horizontalIcon {
    display: flex;
    align-items: center;
    border: 0.2px solid rgba(66, 63, 63, 0.479);
    padding: 2px 6px;
    border-radius: 5px;
    cursor: pointer;
}

/* .interRight */
.interRight > .body_right {
    margin: 1.5vh 0 3vh 0;
}

.interRight > .body_right > p{
    font-size: 14px;
    color: rgba(73, 73, 73, 0.952);
}

/* profile_right */
.profile_right {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.profile_right > .profile1 {
    display: flex;
}

.profile1 > .image {
    width: 25px;
    height: 25px;
    border-radius: 50%;
}

.profile1 > .image > img {
    width: 100%;
    border-radius: 50%;
}

.profile1 > .clock {
    display: flex;
    align-items: center;
    margin-left: 15px;
}

.clock > p {
    margin-left: 6px;
}

.profile_right > .profile2 {
    display: flex;
}

.profile2 > .new_clent {
    width: auto;
    height: 30px;
   
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 6px;
    transition: all 0.3s;
}

.profile2 > .new_clent:hover {
    box-shadow: 0px 0px 1px 1px grey;
}

.new_clent > span {
    margin-left: 6px;
    font-size: 14px;
}

.new_clent > p {
    font-size: 14px;
}

.profile2 > .progress_bar {
    width: 80px;
    height: 30px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 6px;
    margin: 0 4px;
    transition: all 0.3s;
    padding: 0;
}

/* progress bar */
.progress_bar > .progress_item {
    border: 1px solid rgba(194, 194, 194, 0.664);
    background-color: rgb(227, 230, 235);
    width: 70px;
}

.progress_item > .barr {
    border: 2px solid green;
    width: 40px;
}
/* progress bar and */ 

.profile2 > .progress_bar:hover {
    box-shadow: 0px 0px 1px 1px grey;
}

.profile2 > .comlete {
    width: auto;
    height: 30px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 6px;
    transition: all 0.3s;
}

.profile2 > .comlete:hover {
    box-shadow: 0px 0px 1px 1px grey;
}

.comlete > p {
    font-size: 14px;
}

.comlete > span {
    margin-right: 6px;
    font-size: 14px;
}

.profile2 > .comments {
    width: auto;
    height: 30px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 6px;
    margin-left: 4px;
    transition: all 0.3s;
}

.profile2 > .comments:hover {
    box-shadow: 0px 0px 1px 1px grey;
}

.comments > span {
    margin-right: 6px;
    font-size: 14px;
}

.comments > p {
    margin-right: 6px;
    font-size: 14px;
}

.designProject {
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 0px 4px 0.2px rgb(177, 177, 177);
    border-radius: 6px;
    margin-top: 4vh;
}

.designProject > .designProject_wrapper {
    width: 96%;
    margin: auto;
    display: flex;
    padding: 2vh 0;
}

.designProject_wrapper > .design_left {
    width: 5%;
    display: flex;
    margin-right: 10px;
    padding: 3px;
}

.design_left > .raundet {
    width: 25px;
    height: 25px;
    border: 2px solid rgba(129, 129, 129, 0.644);
    border-radius: 50%;
}

.designProject_wrapper > .design_right {
    width: 100%;
}

.design_right > .header_right {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header_right > .interRightItem {
    display: flex;
    align-items: center;
}

.interRightItem > .clock_reminder {
    display: flex;
    align-items: center;
    border: 0.2px solid rgba(204, 204, 204, 0.267);
    padding: 2px 6px;
    
}
.design_right > .header_right > .interRightItem >.clock_reminder {
    border-radius: 20px;
    padding: 3px 10px;
    background-color: rgb(237,188,74);
    cursor: pointer;
}

.design_right > .header_right > .interRightItem  > .priority_reminder {
    display: flex;
    align-items: center;
    border: 0.2px solid rgba(66, 63, 63, 0.479);
    padding: 2px 6px;
    border-radius: 8px;
    margin: 0 10px;
    cursor: pointer;
}

.design_right > .header_right > .interRightItem > .horizontalIcon {
    display: flex;
    align-items: center;
    border: 0.2px solid rgba(66, 63, 63, 0.479);
    padding: 2px 6px;
    border-radius: 5px;
    cursor: pointer;
}

/* .interRight */
.design_right > .body_right {
    margin: 1.5vh 0 3vh 0;
}

.design_right > .body_right > p{
    font-size: 14px;
    color: rgba(73, 73, 73, 0.952);
}

